* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))), url(https://images.unsplash.com/photo-1493711662062-fa541adb3fc8?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=f2e8843e67935b873f32154438cf9664&auto=format&fit=crop&w=750&q=80) no-repeat center;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(https://images.unsplash.com/photo-1493711662062-fa541adb3fc8?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=f2e8843e67935b873f32154438cf9664&auto=format&fit=crop&w=750&q=80) no-repeat center;
  background-attachment: fixed;
  background-size: cover; }

.list {
  width: 90%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0 auto;
  padding: 20px 0; }

.item {
  width: 330px;
  height: 235px;
  margin: 15px 10px;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  -webkit-transition: -webkit-filter .3s, -webkit-box-shadow .2s;
  transition: -webkit-filter .3s, -webkit-box-shadow .2s;
  transition: filter .3s, box-shadow .2s;
  transition: filter .3s, box-shadow .2s, -webkit-filter .3s, -webkit-box-shadow .2s; }

.item:hover {
  cursor: pointer;
  -webkit-box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.4);
          box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.4);
  -webkit-filter: brightness(1.35);
          filter: brightness(1.35); }

.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  max-height: 30%;
  background-color: rgba(23, 22, 22, 0.68);
  padding: 10px;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  -webkit-transition-duration: 1s;
          transition-duration: 1s; }

.content:hover {
  background-color: rgba(253, 18, 69, 0.548); }

.avatar {
  overflow: hidden;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
  -webkit-transition: width .3s, height .3s;
  transition: width .3s, height .3s;
  position: relative; }

.avatar:hover {
  width: 75px;
  height: 75px; }

.avatar img {
  width: 100%;
  position: relative;
  opacity: 0;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in; }

.desc {
  color: white;
  max-width: 300px; }

.preview, .preview::before,
.avatar::before, .preview img {
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em; }

.preview {
  height: 70%;
  position: relative; }

.preview::before,
.avatar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/jpeg;base64,");
  background-repeat: no-repeat;
  background-size: cover; }

.preview img {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in; }

.list p {
  max-width: 220px;
  /* no newline */
  white-space: nowrap;
  /* crop text */
  overflow: hidden;
  /* ... */
  text-overflow: ellipsis; }

.btnLoadNext20 {
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 9999; }

.title {
  font-size: 48px;
  color: white;
  text-align: center;
  margin-top: 20px; }

.lang {
  font-size: 24px;
  text-align: center; }

.lang div {
  display: inline-block;
  color: white;
  margin-right: 10px;
  height: 30px; }

.lang div:hover {
  background-color: skyblue;
  cursor: pointer; }

.lang-selected {
  background-color: skyblue;
  cursor: none; }
